import './TechPage.css'
import React, {useEffect} from 'react'
import {useAuthContext} from '../../../contexts/AuthContext'
import {createContent} from '../../../services/ApiClient'
import {Helmet} from 'react-helmet'
import Banner from './Banner/Banner'
import Video from './Video/Video'
import Map from './Map/Map'
import BottomCta from './BottomCta/BottomCta'
import VerticalCarousel from './VerticalCarousel/VerticalCarousel'

function TechPage() {
    const {user} = useAuthContext()
    const data = {
        content: [],
        url: '/tecnologia',
        name: 'Tecnologa',
    }

    useEffect(() => {
        if (user) {
            const mainContent = document.querySelectorAll('p, h1, h2, h3, h4, h5, h6')
            mainContent.forEach(content => {
                data.content.push(content.innerText)
            })

            const fetchData = async () => {
                await createContent(data)
            }
            fetchData()
        }

        const isMenuOpen = document.querySelector('.show')
        if (isMenuOpen) {
            isMenuOpen.classList.remove('show')
        }

        const isCloseSearch = document.querySelector('.Header__search-close')
        if (isCloseSearch) {
            isCloseSearch.classList.remove('Header__search-close')
        }

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])
    return (
        <>
            <Helmet>
                <title>Grupo Leti | Tecnologa</title>
                <meta name="description" content="Nuestra planta est ubicada en Guarenas, estado Miranda, y es la planta produccin de frmacos ms grande a nivel nacional. Cuenta con la nica planta de cefalospornicos existentes en Venezuela y una de las pocas penicilnicos, siendo modelo de Latinoamrica." />
                <meta name="keywords" content="Grupo Leti, Tecnologa" />
            </Helmet>
            <main className="Tech">
                <Banner />
                <Video />
                <VerticalCarousel />
                <Map />
                <BottomCta />
            </main>
        </>
    )
}

export default TechPage
