import React, {useEffect} from 'react'
import {useAuthContext} from '../../../contexts/AuthContext'
import {createContent} from '../../../services/ApiClient'
import {Helmet} from 'react-helmet'
import Banner from './Banner/Banner'
import InfoCards from './InfoCards/InfoCards'
import Letter from './Letter/Letter'
import Megat from './Bottom/Bottom'

function OurPhilosophyPage() {
    const {user} = useAuthContext()
    const data = {
        content: [],
        url: '/nuestra-filosofia',
        name: 'Nuestra filosofa',
    }

    useEffect(() => {
        if (user) {
            const mainContent = document.querySelectorAll('p, h1, h2, h3, h4, h5, h6')
            mainContent.forEach(content => {
                data.content.push(content.innerText)
            })

            const fetchData = async () => {
                await createContent(data)
            }
            fetchData()
        }

        const isMenuOpen = document.querySelector('.show')
        if (isMenuOpen) {
            isMenuOpen.classList.remove('show')
        }

        const isCloseSearch = document.querySelector('.Header__search-close')
        if (isCloseSearch) {
            isCloseSearch.classList.remove('Header__search-close')
        }

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])
    return (
        <>
            <Helmet>
                <title>Grupo Leti | Nuestra filosofa</title>
                <meta name="description" content="Todos los lderes de cada unidad y dems reas de trabajo, trabajan en conjunto para promover la relacin de sinergia entre todas las empresas y as lograr los mejores resultados. Contamos con un talento humano excepcional y altamente calificado que trabaja da a da generando soluciones para los venezolanos, bajo los principios y tica del grupo.." />
                <meta name="keywords" content="Grupo Leti, Nuestra filosofa" />
            </Helmet>
            <main>
                <Banner />
                <InfoCards />
                <Letter />
                <Megat />
            </main>
        </>
    )
}

export default OurPhilosophyPage
