/* eslint-disable no-redeclare */
export const seoURL = (str) => {
    return str.toString()
        .normalize('NFD')
        .replace(/[\u0300-\u036f]/g, '')
        .replace(/\s+/g, '-')
        .toLowerCase()
        .replace(/&/g, '-and-')
        // eslint-disable-next-line
        .replace(/[^a-z0-9\-]/g, '')
        .replace(/-+/g, '-')
        .replace(/^-*/, '')
        .replace(/-*$/, '')
}

export const truncate = (string, n) => {
    return string?.length > n ? string.substr(0, n - 1) + '...' : string
}

export const drawTime = (time) => {
    const months = ["Enero", "Febrero", "Marzo", "Abril", "Mayo", "Junio", "Julio", "Agosto", "Septiembre", "Octubre", "Noviembre", "Diciembre"]
    const days = ["Domingo", "Lunes", "Martes", "Mircoles", "Jueves", "Viernes", "Sbado"]
    const d = new Date(time)
    const day = days[d.getDay()]
    const date = d.getDate()
    const month = months[d.getMonth()]
    const year = d.getFullYear()
    return day + " " + date + " " + month + " " + year
}

export const JSONToCSVConvertor = (JSONData, ReportTitle, ShowLabel) => {
    //If JSONData is not an object then JSON.parse will parse the JSON string in an Object
    var arrData =
        typeof JSONData !== "object" ? JSON.parse(JSONData) : JSONData

    var XLS = ""

    //This condition will generate the Label/Header
    if (ShowLabel) {
        var row = ""

        //This loop will extract the label from 1st index of on array
        for (var index in arrData[0]) {
            //Now convert each value to string and comma-seprated
            row += index + ","
        }

        row = row.slice(0, -1)

        //append Label row with line break
        XLS += row + "\r\n"
    }

    //1st loop is to extract each row
    for (var i = 0; i < arrData.length; i++) {
        var row = ""

        //2nd loop will extract each column and convert it in string comma-seprated
        for (var index in arrData[i]) {
            row += '"' + arrData[i][index] + '",'
        }

        row.slice(0, row.length - 1)

        //add a line break after each row
        XLS += row + "\r\n"
    }

    if (XLS === "") {
        alert("Invalid data")
        return
    }

    //Generate a file name
    var fileName = "Reporte_FarmacoVigilancia"
    //this will remove the blank-spaces from the title and replace it with an underscore
    fileName += ReportTitle.replace(/ /g, "_")

    //Initialize file format you want csv or xls
    var uri = "data:text/xls;charset=utf-8," + escape(XLS)

    // Now the little tricky part.
    // you can use either>> window.open(uri);
    // but this will not work in some browsers
    // or you will not get the correct file extension

    //this trick will generate a temp <a /> tag
    var link = document.createElement("a")
    link.href = uri

    var currentDate = new Date()

    //set the visibility hidden so it will not effect on your web-layout
    link.style = "visibility:hidden"
    link.download = currentDate.getDate() + '_' + currentDate.getMonth() + '_' + currentDate.getFullYear() + '_' + fileName + ".xls"

    //this part will append the anchor tag and remove it after automatic click
    document.body.appendChild(link)
    link.click()
    document.body.removeChild(link)
}